
* {
  margin: 0;
  padding: 0;
}
html,
body {
  background: #51b8cb;
}
#app {
  width: 10rem;
  height: 100%;
  overflow: hidden;
  margin: 0 auto;
}
@charset "UTF-8";
.qrcodeContain {
  width: 9.333333rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  font-size: 0.453333rem;
  background: #fff;
  margin: 0.666667rem 0.4rem;
  padding-bottom: 0.533333rem;
  letter-spacing: 1px;
  border-radius: 0.2rem;
  -webkit-box-shadow: 0 0.2rem 0.333333rem #888888;
          box-shadow: 0 0.2rem 0.333333rem #888888;
}
.qrcodeContain #canvas {
  width: 6rem !important;
  height: 6rem !important;
}
.qrcodeContain .title {
  text-align: center;
  padding: 0.533333rem 0 0 0;
}
.qrcodeContain .detail {
  width: 90%;
  padding: 0.133333rem 0;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  overflow: hidden;
  /*父元素*/
  color: #727171;
}
.qrcodeContain .detail .label {
  float: left;
  /*子元素*/
  font-size: 0.4rem;
}
.qrcodeContain .detail .info {
  overflow: hidden;
  /*子元素*/
  font-size: 0.4rem;
}
.qrcodeContain .msg {
  font-size: 0.32rem;
  color: #51b8cb;
}